@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');

:root {
  font-size: 14px;
  font-weight: regular;
  color: #000;
  text-align: left;
}

* {
  font-family: "Roboto Condensed", sans-serif;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  border: none;
  text-align: inherit;
}


